<template>
    <div>
        <ConsoleHeader />
      
        <div class="content-wrapper">
            <!-- Content Header (Page header) -->
            <div class="content-header">
                <div class="container-fluid">
                    <div class="row mb-2">
                    <div class="col-sm-6 text-start">
                        <h1 class="m-0 text-dark">Profile</h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                            <li class="breadcrumb-item"><router-link to="/console">Home</router-link></li>
                            <li class="breadcrumb-item active">Profile</li>
                        </ol>
                    </div><!-- /.col -->
                    </div><!-- /.row -->
                </div><!-- /.container-fluid -->
            </div>
            <!-- /.content-header -->

            <!-- Main content -->
            <section class="content">

                <form method="POST" @submit.prevent="submitForm">
                    
                    <div class="container-fluid">
                    
                        <!-- Content Row -->
                        <div class="row p-0">
                                
                            <div class="col-xl-12 col-md-12">

                                <!-- XHR Response -->
                                <div id="response"></div>

                                <div class="form-inline mb-2">
                                    <div class="form-group mr-3">
                                        <input type="file" name="file_holder" ref="profile_picture" class="d-none" id="profile_image" @change.prevent="changeImage" />
                                        <label for="profile_image" style="cursor: pointer;" v-if="user.profile_picture === null || user.profile_picture === ''"><img src="@/assets/images/profile.png" class="img-fluid" style="width: 5em;"/></label>
                                        <label for="profile_image" style="cursor: pointer;" v-else ><img :src="user.profile_picture" class="img-fluid" style="width: 5em;"/></label>
                                    </div>
                                    
                                    <div class="form-group">
                                        <h3 class="">
                                            <span ng-bind="firstname"></span> <a href="javascript:void(0)" onclick="$('input[name=\'firstname\']').focus()" class="btn btn-link text-dark"><i class="mdi mdi-pencil mdi-24px"></i></a>
                                            {{user.first_name}} {{user.last_name}}
                                        </h3>
                                        
                                    </div>
                                </div>

                                <div class="d-flex">
                                    <div class="alert alert-info alert-sm p-2 d-none" id="image_name_holder"></div>
                                </div>
                                

                            </div>
                            
                            <div class="col-xl-12 col-md-12 mb-4">
                                <div class="card">
                                    <div class="card-header">
                                        <div class="row">
                                            <h5 class="card-title mb-0 pb-0 col font-weight-bold text-start">Profile Information</h5>
                                            <!-- Button trigger modal -->
                                            
                                        </div>
                                    </div>
                                    
                                    <div class="card-body white-bg">


                                        <div class="form-group row">
                                            <div class="col text-start">
                                                <label for="">First Name</label>
                                                <input type="text" name="firstname" class="form-control remove-radius" placeholder="First Name" v-model="user.first_name" required/>
                                            </div>
                                            <div class="col text-start">
                                                <label for="">Last Name</label>
                                                <input type="text" name="lastname" class="form-control remove-radius" placeholder="Last Name" v-model="user.last_name" required/>
                                            </div>
                                        </div>
                                        <div class="form-group text-start">
                                            <label for="">Email Address</label>
                                            <input type="email" name="email" class="form-control remove-radius" placeholder="Email Address" v-model="user.email" disabled required/>
                                        </div>
                                        <div class="form-group text-start">
                                            <label for="">Country</label>
                                            <select name="country" class="custom-select remove-radius reg" id="" v-model="user.country" placeholder="--Select Country--" required>
                                                <option value="" ng-hide="user_data.country !== null || user_data.country !== ''" disabled>--Select Country--</option>
                                                <option v-for="country in countries" v-bind:key="country">{{country}}</option>
                                            </select>
                                        </div>
                                        <div class="form-group text-start">
                                            <label for="">Phone Numbers</label>
                                            <div class="input-group">
                                                <div class="input-group-prepend">
                                                    <div class="input-group-text">+234</div>
                                                </div>
                                                <input type="text" name="tel" class="form-control remove-radius" placeholder="Phone Number 1" v-model="user.tel" required>
                                            </div>
                                        </div>
                                        
                                        <!-- Office address and location -->

                                        <div class="form-group row">
                                            <div class="col text-start">
                                                <label for="">State</label>
                                                <input type="text" name="state" class="form-control remove-radius" placeholder="State" v-model="user.state" required/>
                                            </div>
                                        
                                        </div>

                                        <div class="form-group text-start">
                                            <label for="">Main Address</label>
                                            <textarea class="form-control remove-radius admin_bio_editor" name="main_address" v-model="user.home_address" placeholder="Enter Main Address" required></textarea>
                                        </div>

                                        <div class="clearfix"></div>

                                        <button type="submit" class="btn btn-success float-right mb-0 mt-0" >Save</button>

                                    </div>

                                </div>
                            </div>
                            
                        </div>
                    

                    </div>

                </form>
                

            </section>
            <!-- /.content -->

        </div>

        <ConsoleFooter />
    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";

import Vue from'vue';
import {mapActions, mapGetters} from 'vuex';

import axios from 'axios';
import AuthenticationService from '@/services/AuthenticationService';

import keys from '@/lib/keys';
import Cloudinary, { CldImage, CldContext } from "cloudinary-vue";
Vue.use(Cloudinary, {
  configuration: { cloudName: keys.cloudinary_name },
  components: {
    CldImage, CldContext
  }
});


export default {
    name: "ConsoleProfile",
    data(){
        return{
            profile_picture: '',
            cloudName: 'cacsafpi'
        }
    },
    computed: {
        ...mapGetters(['user', 'countries'])
    },
    components: {
        ConsoleHeader,
        ConsoleFooter
    },
    methods: {  
        ...mapActions(['update_account', 'user_account']),
        async submitForm(){
            try{
                await this.update_account({id: this.user.id, user: this.user});
                // await this.user_account(this.user.id);
            }
            catch(err){
                console.log(err);
            }
        },
        async changeImage(){
            this.profile_picture = this.$refs.profile_picture.files[0];
            let formData = new FormData();
            formData.append('upload_preset', 'images');
            formData.append('tags', 'unsigned upload, images');
            formData.append('file', this.profile_picture);

            let response = await axios.post(`https://api.cloudinary.com/v1_1/cacsafpi/upload`, formData);

            if (response.data.secure_url !== null) {
                this.user.profile_picture = response.data.secure_url;
                let old_image = this.user.profile_picture;
                await this.update_account({id: this.user.id, user: this.user});

                if (old_image !== null) {
                    await AuthenticationService.removeImage({old_image});
                }

            }
        }
    }

}
</script>

